@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {
  color: unset;
}

#main {
  margin-top: -18px;
}

#cta {
  margin-top: -2%;
  color: #0c2f4e;
  margin-bottom: 30px;
  text-align: center; /* Center align the text (optional) */
  font-size: 17px; /* Initial font size, adjust as needed */
  font-weight: bold; /* Bold text to enhance visibility */
}

#title {
  font-size: 70px;
  font-family: 'Anton', sans-serif;
  text-align: center;
  font-style: italic;
  font-weight: 600;
  color: black;
  text-shadow: -4px 4px 8px rgba(0, 0, 0, 0.7);
  position: relative;
  top: -33px;
}

.list {
  text-align: left;
  list-style-position: inside;
  margin-right: 15%;
}
.toolz {
  background: -webkit-linear-gradient(45deg, #ff2bbc, #0c2f4e);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 15pt;
}

#bg {
  margin: 27px 3%;
  padding-top: 10px;
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(54, 54, 54, 0.37);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}
#bg::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  border-radius: 10px; /* Match parent's border-radius */
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0.45),
    /* More opaque at the sides */ rgba(220, 251, 211, 0),
    /* More transparent at the center */ rgba(255, 255, 255, 0.041)
      /* More opaque at the sides */
  );
}

nav {
  padding: 10px;
}

#list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  margin-right: 2em;
  margin-left: 0;
  margin-bottom: -4px;
  font-family: 'Anton', sans-serif;
  font-style: italic;
}

.items {
  background-color: white;
  margin-right: 10px;
}
.items a {
  color: black;
}

.items:last-child {
  margin-right: 0;
}

.home-link {
  margin-right: auto;
}

